				align-items: center;
				transition: all 0.2s linear;

				.suggestion-list-icon {
					--bg-color: #eafffa;
					width: var(--grid-first);
					height: var(--grid-first);
					border-radius: 10px;
					display: grid;
					place-items: center;
					background-color: var(--bg-color) !important;

					&.bg-success {
						--bg-color: #eafffa;
					}

					&.bg-danger {
						--bg-color: #f7e2e2;
					}
				}

                &:hover {
                    background-color: var(--theme-clr-green-light) !important;
                }
			}
		}
	}
</style>
